import {config} from "../config";
import * as EJSON from "ejson";


class Store {
    protected static readonly SERIALIZE_PREFIX = "___s";
    protected syncWithServer = false; // TODO option to pass WS in constructor to sync with backend

    constructor() {
    }

    public getItem(key: string): any {
        let value = localStorage.getItem(key);
        if (value !== null && value.substr(0, Store.SERIALIZE_PREFIX.length) === Store.SERIALIZE_PREFIX)
            value = this.unserialize(value);
        return value;
    }

    public setItem(key: string, value: any) {
        if (typeof value !== "string")
            value = this.serialize(value); // localStorage only supports strings
        localStorage.setItem(key, value);
    }

    public removeItem(key: string) {
        localStorage.removeItem(key);
    }

    public clear() {
        localStorage.clear();
    }

    public getLength() {
        return localStorage.length;
    }

    public keyName(index: number) {
        return localStorage.key(index);
    }

    // ################################################################
    // ###################### PRIVATE FUNCTIONS #######################

    protected serialize(value: any): string {
        if (config.jsonStoreFormat === "EJSON")
            return Store.SERIALIZE_PREFIX + EJSON.stringify(value);
        return Store.SERIALIZE_PREFIX + JSON.stringify(value);
    }

    protected unserialize(value: string): any {
        if (value && value.substr(0, Store.SERIALIZE_PREFIX.length) === Store.SERIALIZE_PREFIX)
            value = value.substr(Store.SERIALIZE_PREFIX.length);
        if (config.jsonStoreFormat === "EJSON")
            return EJSON.parse(value);
        return JSON.parse(value);
    }
}

let store = new Store();
export {store}
